.videoCard{
    color: white;
    padding: 10px 10px;
    margin: 20px 10px ;
    cursor: pointer;
    transition: transform 100ms ease-in-out;
    border: 1px solid #051421;
    box-shadow: 0px 0px 8px 2px #051421;
}
.videoCard > img{
    object-fit: contain;
    height: 350px;
    border-radius: 4%;
    margin: 10px 0px;
    transition: transform 100ms ease-in;
}
.videoCard:hover > img{
    transform: scale(1.05);
}